/* open-sans-regular - latin-ext_latin */
@font-face {
  font-family: "CV-SH-OpenSans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/open-sans-v15-latin-ext_latin/open-sans-v15-latin-ext_latin-regular.woff2")
      format("woff2"),
    /* Chrome 26+, Opera 23+, Firefox 39+ */
      url("../fonts/open-sans-v15-latin-ext_latin/open-sans-v15-latin-ext_latin-regular.woff")
      format("woff"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* open-sans-700 - latin-ext_latin */
@font-face {
  font-family: "CV-SH-OpenSans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/open-sans-v15-latin-ext_latin/open-sans-v15-latin-ext_latin-700.woff2")
      format("woff2"),
    /* Chrome 26+, Opera 23+, Firefox 39+ */
      url("../fonts/open-sans-v15-latin-ext_latin/open-sans-v15-latin-ext_latin-700.woff")
      format("woff"); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
